import React, { Component } from 'react';
import Navbar from "./Navbar";
import Calendar from 'react-calendar';
import Footer from "./footer";
import 'react-calendar/dist/Calendar.css';

export default class Home extends Component {
  state ={
    date:new Date(),
  }
  onChange=date =>this.setState({date})
  render() {
    return (
      <div>
        <section className="heading-section">
      <div className="container d-flex justify-content-center">
        <div><h1 className="heading-name">فضيلة الشيخ ظفر الحسن مدني حفظه الله</h1></div>
      <hr/>
      </div>
      <div className="text-center mt-0">
      <p className="mt-0">SHAIKH ZAFAR UL HASAN MADANI</p>
      </div>
          <Navbar/>
      </section>
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-12">
          <section className="banner-section">
      <div id="carouselExampleInterval" className="carousel slide" data-ride="carousel">
  <div class="carousel-inner">
    <div class="carousel-item active" data-interval="10000">
      <img src="..." className="d-block w-100" alt="..." height="400px" width="100%"/>
    </div>
    <div class="carousel-item" data-interval="2000">
      <img src="..." className="d-block w-100" alt="..." height="400px" width="100%"/>
    </div>
    <div class="carousel-item">
      <img src="..." className="d-block w-100" alt="..." height="400px" width="100%"/>
    </div>
  </div>
  <a className="left carousel-control" href="#myCarousel" data-slide="prev">
      <span className="glyphicon glyphicon-chevron-left"></span>
      <span className="sr-only">Previous</span>
    </a>
    <a class="right carousel-control" href="#myCarousel" data-slide="next">
      <span className="glyphicon glyphicon-chevron-right"></span>
      <span className="sr-only">Next</span>
    </a>
</div>
      </section>
          </div>
        </div>
      </div>
      <section className="embed-section">
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-3">
            <section className="calendar-section">
            <Calendar
            onChange={this.onChange}
            value={this.state.date}
            />
            </section>
          </div>
          <div className="col-md-9">
          <section className="mixlr-section">
            <h1>Mixlr Section</h1>
          </section>
          </div>
          </div>
          </div>
          </section>
          <section className="embed-section">
          <div className="container-fluid">
      <div className="row">
      <div className="col-md-3">
      <section className="telegram-section">
        <h1>Youtube section</h1>
        </section>
        </div>
        <div className="col-md-9">
          <section className="latest-section">
            <h1>Latest section</h1>
          </section>
          </div>
      </div>
      </div>
          </section>
          <div className="col-md-12 text-center"><Footer/></div>
      </div>
    )
  }
}
